import React, { cloneElement, forwardRef } from 'react';
import { bool, element, node, oneOf, string } from 'prop-types';
import classnames from 'classnames';
import ButtonLabel from '../button-label';

const CSS_PREFIX = 'vds-btn-link';
const CSS_STATE = 'vds-state';

const ButtonLink = forwardRef(({
  children,
  className,
  colorScheme,
  disabled,
  tag,
  ...remainingProps
}, ref) => (
  cloneElement(tag, {
    children: tag.type === 'button' ? <ButtonLabel>{children}</ButtonLabel> : children,
    className: classnames({
      [`${CSS_PREFIX}`]: Boolean(colorScheme) === false,
      [`${CSS_PREFIX}--${colorScheme}`]: Boolean(colorScheme),
      [`${CSS_STATE}--disabled`]: Boolean(disabled)
    }, className),
    disabled,
    ref,
    ...remainingProps
  })
));

ButtonLink.propTypes = {
  /**
   * @ignore
   */
  children: node,

  /**
   * @ignore
   */
  className: string,

  /**
   * Color scheme of button
   */
  colorScheme: oneOf([
    'primary',
    'secondary',
    'tertiary'
  ]),

  /**
  * Disabled
  */
  disabled: bool,

  /**
  * Tag
  */
  tag: element
};

ButtonLink.defaultProps = {
  tag: <button /> // eslint-disable-line
};

ButtonLink.displayName = 'ButtonLink';

export default ButtonLink;

